import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import { animateScroll } from "react-scroll";

import SocketContext from '../../socket-context'
import {Col, Container, Row} from 'reactstrap'
import { Button , TextField } from '@material-ui/core'
import * as AdminAction from '../../redux/actions/adminAction';
import * as AuthAction from '../../redux/actions/authAction';

import './Admin.css'
class AdminInvoiceDetail extends Component{
    state={
        msg:''
    }
    componentDidMount(){
        let item = this.getCurrentInquiry()
        this.props.socket.emit('checkDoneInquiryMsg',{
            inquiry:item.inquiry._id,
            receiver:this.props.auth.user.id
        })
        this.props.socket.on('__sendInvoiceMsg',data=>{
            this.props.socket.emit('checkDoneInquiryMsg',{
                inquiry:item.inquiry._id,
                receiver:this.props.auth.user.id
            })
        })
        this.props.socket.on('__checkDoneInquiryMsg',data=>{
            this.props.checkDoneInquiryMsg(item.inquiry._id)
        })
        this.scrollToBottom()

    }
    componentDidUpdate() {
        this.scrollToBottom();
    }
    
    
    onMessageChange = (event) =>{
        this.setState({
            msg:event.target.value
        })
    }
    getCurrentInquiry = ()=>{
        let returnVal = null
        console.log(this.props.currentinquiry)
        
        if(Array.isArray(this.props.admin.askInquires))
        {
            this.props.admin.askInquires.map((ite,index)=>{
                if(ite.inquiry._id == this.props.currentinquiry){
                    returnVal = ite
                }
            })
        }
        console.log(returnVal)
        return returnVal
    }
    scrollToBottom() {
        animateScroll.scrollToBottom({
          containerId: "speechContainer"
        });
    }
    onAcceptInvoice = () =>{
        this.props.acceptInvoice({
            uid         :this.props.auth.user.id,
            invoiceId   :this.props.currentinquiry
        })
        this.props.onBack()
    }
    render(){
        const currentInquiry = this.getCurrentInquiry()
        return(
            <div style={{width:'100%'}}>
                <div style={{
                    display: 'flex',
                    justifyContent: 'space-between',
                    paddingLeft: '20px',
                    paddingRight: '20px'
                }}>
                
                <Button
                            variant="contained"
                            style={{backgroundColor:'#fc5555!important'}}
                            // startIcon={<AddIcon />}
                            onClick={this.props.onBack}
               
                            
                        >
                            Back
                </Button>
                <Button
                            variant="contained"
                            // startIcon={<AddIcon />}
                            onClick={()=>{this.onAcceptInvoice()}}
                            
                            // style={{backgroundColor:"black"}}
                            className="NormalBtn"
                        >
                            Accept Invoice
                </Button>
                </div>
                {
                    currentInquiry && Array.isArray(currentInquiry.chatlogs) && (
                        <div id="speechContainer"  className="InvoiceSpeech">
                        {currentInquiry.chatlogs.map((chat,index)=>{

                            if(chat.sender._id != this.props.auth.user.id){
                                return(
                                    <div className="Speech-Left Speech-Item">
                                        <img src="/static/assets/img/avatar1.png" className="Avatar"></img>
                                        <div className="Speech-Content">
                                            <p className="Speech-Time">10:46 AM</p>
                                            <div className="Speech-Detail">
                                                <div className="Speech-Detail-Title">
                                                    {chat.sender.username}
                                                </div>
                                                <div className="Speech-Detail-Content">
                                                    <p>{chat.msgcontent}</p>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            }else
                            {
                                return(
                                    <div className="Speech-Right Speech-Item">
                                        <div className="Speech-Content">
                                            <p className="Speech-Time">10:46 AM</p>
                                            <div className="Speech-Detail">
                                                <div className="Speech-Detail-Title">
                                                    Me
                                                </div>
                                                <div className="Speech-Detail-Content">
                                                    <p>{chat.msgcontent}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <img src="/static/assets/img/avatar1.png" className="Avatar"></img>
                                    </div>
                                )
                            }
                        })}
                        </div>
                    )
                }
                <div style={{margin:'20px',display:'flex',justifyContent:'space-around'}}>
                    <TextField 
                            id="filled-basic" 
                            label="Message" 
                            variant="outlined"
                            onKeyPress={(e)=>{
                                if (e.key === 'Enter') {
                                    if(this.state.msg != ''){
                                        this.setState({msg:''})
                                        this.props.sendmsg(this.state.msg)
                                    }
                                  }
                            }}
                            fullWidth
                            onChange={this.onMessageChange}
                            className="NormalInput"
                        />
                        <Button
                            style={{width:'50px',margin:'5px'}}
                            variant="contained"
                            // startIcon={<AddIcon />}
                            onClick={()=>{
                                if(this.state.msg != ''){
                                    this.setState({msg:''})
                                    this.props.sendmsg(this.state.msg)
                                }
                            }}
                            
                            // style={{backgroundColor:"black"}}
                            className="NormalBtn"
                        >
                            Send
                        </Button>

                       
                </div>
            </div>
        )
    }
}
const AdminInvoiceDetailWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <AdminInvoiceDetail {...props} socket={socket} />}
    </SocketContext.Consumer>
  )
const mapStateToProps = (state) =>({
    auth    :state.auth,
    admin :state.admin
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({ 
        acceptInvoice         :AdminAction.acceptInvoice,
      checkDoneInquiryMsg   :AdminAction.checkDoneInquiryMsg

      //sendInvoice           :InquiryAction.sendInvoice,
      //checkDoneInquiryMsg   :InquiryAction.checkDoneInquiryMsg
      //setNewChatLogs  :GlobalAction.setNewChatLogs,

      // showDlg:GlobalAction.showDialog
      //  registerUser :AuthAction.registerUser
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminInvoiceDetailWithSocket)