const {
    TOGGLE_SIDE_MENU, 
    ADMIN_SET_TITLE,
    GET_INVOICE_LIST,
    GET_ASK_INVOICE_CAR,
    NEW_INVOICE_MSG,
    ADMIN_CHECK_DONE_INQUIRY_MSG,
    ADMIN_ACCEPT_INVOICE,
    GET_ORDER_INVOICE_CAR
} = require('../actions/types');

const initialState = {
    showSideBar :false,
    title:"",
    askInquires :[],
    orders      :[]
}
export default function(state = initialState,action){
    switch(action.type){
        case TOGGLE_SIDE_MENU:
            return{
                ...state,
                showSideBar:!state.showSideBar
            }
        case ADMIN_SET_TITLE:
            return{
                ...state,
                title:action.payload
            }
        case GET_INVOICE_LIST:

            return{
                ...state,
                askInquires :action.payload.askInquires,
                orders      :action.payload.orders
            }
            
        case GET_ASK_INVOICE_CAR:
            console.log(action.payload)
            console.log("GET_ASK_INVOICE_CAR")
            return{
                ...state,
                askInquires:action.payload
            }
        case NEW_INVOICE_MSG:
            console.log("NEW_INVOICE_MSG")

            let inquires = [...state.askInquires]
            let newchatlog = action.payload
            let currentInquiry = null
            let currentIndex = -1
            currentInquiry = inquires.find((inquiry,index)=>{
                if(inquiry.inquiry._id == newchatlog.inquiry)
                {
                    //currentInquiry = inquiry
                    currentIndex = index
                    return true
                }
            })
            if(currentIndex != -1)
            {
                console.log('in')
                let chatId = currentInquiry.chatlogs.indexOf(newchatlog)
                if(chatId == -1){
                    currentInquiry.chatlogs.push(newchatlog)
                }
                inquires[currentIndex] = currentInquiry
            }
            return{
                ...state,
                askInquires:inquires
            }
        case ADMIN_CHECK_DONE_INQUIRY_MSG:
            return{
                ...state
            }

        case ADMIN_ACCEPT_INVOICE:
            let updateInquiry = action.payload.order.inquiry
            let ask = [...state.askInquires]
            let inquirIndex = -1
            ask.find((item,index)=>{
                if(item.inquiry._id == updateInquiry._id){
                    inquirIndex = index
                    return true
                }
            })
            ask.splice(inquirIndex,1)

            let orders = [...state.orders]
            orders.push(action.payload.order)

            return{
                ...state,
                askInquires  :ask,
                orders      :orders
            }
        case GET_ORDER_INVOICE_CAR:
            return{
                ...state
            }

        default:
            return state
    }
}