const {SET_ALL_INQUIRY,INQUIRY_SET_CURRENT_PAGE,SET_ASK_INVOICEPANEL,SEND_INVOICE,ADD_NEW_INVOICE_MSG,CHECK_DONE_INQUIRY_MSG} = require('../actions/types');

const initialState = {
    page:0,
    currentInvoice:0,
    inquiries   :[],
    orders      :[]
}
export default function(state = initialState , action){
    switch(action.type)
    {
        case INQUIRY_SET_CURRENT_PAGE:
            {
                return{
                    ...state,
                    page:action.payload
                }
            }
        case SET_ALL_INQUIRY:
            {
                console.log(action.payload)
                return {
                    ...state,
                    inquiries   :action.payload.myInquiry,
                    orders      :action.payload.myOrder
                }
            }
        case SET_ASK_INVOICEPANEL:
            {
                
                return {
                    ...state,
                    currentInvoice:action.payload
                }
            }

        case SEND_INVOICE:
            {
                let updatedInquiry = action.payload.inquiry
                let inquiryarray = [...state.inquiries]
                inquiryarray.find((o,i)=>{
                    if(o._id == updatedInquiry._id){
                        inquiryarray[i] = updatedInquiry
                        return true
                    }
                })
                return{
                    ...state,
                    inquiries:inquiryarray,
                    currentInvoice:updatedInquiry
                }
            }
        case ADD_NEW_INVOICE_MSG:
            {
                console.log("ADD_NEW_INVOCIE")
                let inquires = state.inquiries
                let newchatlog = action.payload
                let currentInquiry = null
                let currentIndex = -1
                inquires.find((inquiry,index)=>{
                    if(inquiry.inquiry._id == newchatlog.inquiry)
                    {
                        currentInquiry = inquiry
                        currentIndex = index
                        return true
                    }
                })

                if(currentIndex != -1)
                {
                    console.log('in')
                
                    currentInquiry.chatlogs.push(newchatlog)
                    inquires[currentIndex] = currentInquiry
                }
                console.log(currentInquiry)
                
                return{
                    ...state,
                    inquiries:inquires
                }
            }
        case CHECK_DONE_INQUIRY_MSG:
            {
                let inquires = [...state.inquiries]
                let inquiryId = action.payload
                let currentInquiry = null
                let currentIndex = -1
                inquires.find((inquiry,index)=>{
                    if(inquiry.inquiry._id == inquiryId)
                    {
                        currentInquiry = inquiry
                        currentIndex = index
                        return true
                    }
                })
                if(currentIndex != -1)
                {
                    currentInquiry.chatlogs.map((chat,item)=>{
                        chat.state = 0
                    })
                    inquires[currentIndex] = currentInquiry
                }
                return{
                    ...state,
                    inquiries:inquires
                }
            }
        default:
            return state
    }
}