const {SHOW_DIALOG,HIDE_DIALOG,SET_CURRENCY,SET_SOCKET,SET_NEW_CHATLOG} = require('../actions/types');

const initialState = {
    dlgInfo         :{
        type:'',
        title:'',
        msg:''
    },
    showDialog      :false,
    currentCurrency :'USD',
    socket          :null
}
export default function(state = initialState, action){
    switch(action.type){
        case SHOW_DIALOG:
            return {
                ...state,
                showDialog :true,
                dlgInfo:action.payload
            }
        case HIDE_DIALOG:
            return {
                ...state,
                showDialog:false
            }
        case SET_CURRENCY:
            return{
                ...state,
                currentCurrency:action.payload
            }
        case SET_SOCKET:
            return{
                ...state,
                socket:action.payload
            }
      
        default:
            return state;
    }
}