import React, {Component} from 'react';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as HomeAction from '../redux/actions/homeAction';
import * as GlobalAction from '../redux/actions/globalAction'
import { makeStyles } from '@material-ui/core/styles';
import {withStyles, MuiThemeProvider} from '@material-ui/core/styles';

import AdvancedSearch from '../components/AdvancedSearch/AdvancedSearch';
import CarInfo from '../components/CarInfo/CarInfo';
import './Home.css';
import TextField from '@material-ui/core/TextField';
import {Col, Container, Row} from 'reactstrap'
import Button from '@material-ui/core/Button'
import {Paper,Divider,InputBase,IconButton,Avatar} from '@material-ui/core'
import DirectionsIcon from '@material-ui/icons/Directions';
import SearchIcon from '@material-ui/icons/Search';
const styles = theme=> ({
    root: {
      padding: '2px 4px',
      display: 'flex',
      alignItems: 'center',
      width: '100%',
      height:'60px',
      margin:'15px'
    },
    search:{
        marginLeft:'10px',
        opacity:'0.5'
    },
    input: {
      marginLeft: theme.spacing(1),
      flex: 1,
      fontColor:'grey'
    },
    iconButton: {
      padding: 10,
      width:'120px'
    },
    divider: {
      height: 55,
      margin: 4,
    },
  });
let socket = null

class  Home extends Component{

    componentDidMount(){
        if(this.props.auth.isAuthenticated){
            this.props.getGlobalData({
                uid:this.props.auth.user.id
            })
        }
        
    }
    onFilter = () =>{
        this.props.setShowFilter()
    }
    render(){
        const {classes} = this.props;

        return(
            <React.Fragment>
                    <Row>
                        <Col md="12" style={{display:'flex'}}>
                            {/* <TextField 
                                id="outlined-basic"  
                                label="Search" 
                                fullWidth 
                                variant="outlined" 
                                style={{backgroundColor:'white'}}
                            />
                            <Button>Filter</Button> */}
                             <Paper component="form" className={classes.root}>
                                <SearchIcon className={classes.search} />
                                <InputBase
                                    className={classes.input}
                                    placeholder="Search"
                                    
                                />
                            
                                <Divider className={classes.divider} orientation="vertical" />
                                <Button
                                    onClick={this.onFilter}
                                    style={{color:'rgba(73, 154, 236, 1)',paddingLeft:'25px',paddingRight:'25px'}}
                                    startIcon={<Avatar src={'http://www.wpsimplesponsorships.com/wp-content/uploads/2019/05/cropped-icon-256x256.png'} />}
                                >
                                    Filters
                                </Button>
                                
                            </Paper>

                        </Col>
                    </Row>
                    <Row>
                        {this.props.isShowFilter && (
                            <>
                            <Col md="3" sm="12">
                                <AdvancedSearch/>
                            </Col>
                            <Col md="9" sm="12">
                                <CarInfo/>
                            </Col>
                            </>
                        )}
                        {!this.props.isShowFilter && (
                            <>
                            <Col sm="12">
                                <CarInfo/>
                            </Col>
                            </>
                        )}
                   
                    </Row>
                
            </React.Fragment>
        )
    }
    
}
const mapStateToProps = (state) =>({
    auth:state.auth,
    isShowFilter:state.home.isShowFilter,
    cars:state.cars,
    socket:state.global.socket
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        getGlobalData       :HomeAction.getGlobalData,
        getCar              :HomeAction.getCar,
        setShowFilter       :HomeAction.setShowFilter,
        setSocket           :GlobalAction.setSocket

    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)((withStyles(styles, {withThemem: true})(Home)));
