import {SHOW_DIALOG,HIDE_DIALOG,SET_CURRENCY,SET_SOCKET} from './types'

export const showDialog = (param) => dispatch =>{
    console.log("showDialog")
    dispatch({
        type:SHOW_DIALOG,
        payload:param
    })
}
export const hideDialog = (param) => dispatch =>{
    dispatch({
        type:HIDE_DIALOG,
        payload:param
    })
}
export const setCurrentCurrency = (param) => dispatch=>{
    dispatch({
        type:SET_CURRENCY,
        payload:param
    })
}
export const setSocket = (socket) => dispatch=>{
    dispatch({
        type:SET_SOCKET,
        payload:socket
    })
}