import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import SocketContext from '../../socket-context'

import * as InquiryAction from '../../redux/actions/inquiryAction';
import * as AuthAction from '../../redux/actions/authAction';
import * as GlobalAction from '../../redux/actions/globalAction';

import { Button , Tabs , Tab , Paper , TextField } from '@material-ui/core'
import {Col, Container, Row} from 'reactstrap'
import MyCheckBox from '../../components/Utility/MyCheckBox'
import MyInquiryItem from './MyInquiryItem'
import AskInquiry from './AskInquiry'

import "./MyInquiry.css";

// import auth from '../../../../backend/middleware/auth';

class MyInquiry extends Component{
    state = {
        
        name    :'',
        email   :'',
        phone   :'',
        password:'',
        coutnry :'',
        seaport :'',
        address :'',
        //
        myInquiryToggle:false,
        myInquiryItem:0
    }
    componentDidMount(){
        this.props.getAllInquiry({
            userId:this.props.auth.user.id
        })
        this.setState({

        })
        /**socket */
        this.props.socket.on('__sendInvoiceMsgFromAdmin',data=>{
            
            this.props.addNewInvoiceMsg(data.chatlog)
        })
        this.props.socket.on('__successSendInvoiceMsg',data=>{
            this.props.addNewInvoiceMsg(data.chatlog)
        })

    }
    componentWillReceiveProps(nextProps) {
        if (!nextProps.auth.isAuthenticated) {
            this.props.history.push("/");
        }
    }
    onTabChange = (event,newtab) => {
        //this.setState({tab: newtab})
        this.props.setCurrentInquiryPage(newtab)
    }
    onInputValueChange = (e) =>{
        let name = e.target.name
        this.setState({
            name : e.target.value
        })
        /*
        switch(e.target.name)
        {
            case 'name':
                this.setState({
                    name:e.target.value
                })
            break
            case 'email':
                this.setState({
                    name:e.target.value
                })
            break
            case 'phone':
                this.setState({
                    name:e.target.value
                })
            break
        }
        */
    }
    setMyInquiryToggle = (inquiry) =>{

        let itemp = 0
        this.props.setAskInvoicePanel(inquiry)
        
        this.setState({
            myInquiryToggle:!this.state.myInquiryToggle
        })
    }
    render(){
        const { name , email , phone , password , country , seaport , address , myInquiryToggle , myInquiryItem } = this.state
        const { page } = this.props.inquiry
        /*
        let askinquiry = 0
        this.props.inquiry.inquiries.map((item,index)=>{
           if(this.state.myInquiryItem == item._id){
               askinquiry = item
           }
       })
       */
          
        return(
            <div>
                <Row className="TabHeader">
                    <Tabs
                        value={page}
                        onChange={this.onTabChange}
                        indicatorColor="primary"
                        textColor="primary"
                        
                        aria-label="full width tabs example"
                        centered
                        >
                        <Tab label={`MY INQUIRY ( ${this.props.inquiry.inquiries.length} )`}/>
                        <Tab label={`MY ORDERS ( ${this.props.inquiry.orders.length} )`}/>
                        <Tab label="SETTINGS"/>
                    </Tabs>
                </Row>
                {page === 0 && (
                    <Row className="TabContent">
                        {!myInquiryToggle && (
                            <div className="TablePart">
                            <div className="Table-Item TableHeader">
                                <div className="TableContent-Row-Header">
                                    <div className="Table-Item TableHeader-Item THI-Index">
                                        <MyCheckBox></MyCheckBox>
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        PICTURE
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        CAR DETAILS
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        TOTAL PRICE
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        UPDATED
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        CHAT
                                    </div>
                                </div>
                            </div>
                            <div className="TableContent">
                                {this.props.inquiry.inquiries.map((item,index)=>{
                                    return(
                                        <MyInquiryItem key={index} item={item} setToggle={this.setMyInquiryToggle}/>
                                    )
                                })}
                            </div>
                        </div>
                        )}
                        {myInquiryToggle && (
                            <AskInquiry toggle={this.setMyInquiryToggle}/>
                        )}
                    </Row>
                )}
                {page === 1 && (
                    <Row className="TabContent">
                        {(
                            <div className="TablePart">
                            <div className="Table-Item TableHeader">
                                <div className="TableContent-Row-Header">
                                    <div className="Table-Item TableHeader-Item THI-Index">
                                        <MyCheckBox></MyCheckBox>
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        PICTURE
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        CAR DETAILS
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        TOTAL PRICE
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        UPDATED
                                    </div>
                                    <div className="Table-Item TableHeader-Item THI-Normal">
                                        STATUS
                                    </div>
                                </div>
                            </div>
                            <div className="TableContent">

                                 {this.props.inquiry.orders.map((item,index)=>{
                                    return(
                                        <MyInquiryItem key={index} item={item} setToggle={this.setMyInquiryToggle}/>
                                    )
                                })}
                            </div>
                        </div>
                        )}
                    </Row>
                )}
                {page === 2 && (
                    <Row className="TabContent">
                        <div className="SettingPart">
                            <Row>
                                <Col className="Setting-Col-Part" md='4'>
                                    <div className="Setting-Photo">
                                        <div className="Setting-Photo-Item">
                                            <img src='./static/assets/img/car/1.jpg'></img>
                                        </div>
                                        <div className="Setting-Photo-Item">
                                            <Button  color="primary" style={{backgroundColor:'rgba(73, 154, 236, 1)',margin:'20px'}} variant='contained'>
                                                Upload New
                                            </Button>
                                        </div>
                                        <div className="Setting-Photo-Item">
                                            <Button>Delete</Button>
                                        </div>
                                        <div className="Setting-Photo-Item">
                                            <p className="Opacity70">
                                                You can update and delete current photo
                                            </p>
                                        </div>

                                    </div>
                                </Col>
                                <Col className="Setting-Col-Part" md='8'>
                                    <Row style={{height:'100%'}}>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="text"
                                                label="Name"
                                                name="name"
                                                fullWidth
                                                value={name}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="email"
                                                label="Email"
                                                name="email"
                                                fullWidth
                                                value={email}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="text"
                                                label="Phone"
                                                name="phone"
                                                fullWidth
                                                value={phone}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="password"
                                                label="Password"
                                                name="password"
                                                fullWidth
                                                value={password}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="text"
                                                label="Country"
                                                name="country"
                                                fullWidth
                                                value={country}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="text"
                                                label="Seaport"
                                                name="seaport"
                                                fullWidth
                                                value={seaport}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                        <Col md='6'>
                                            <TextField
                                                id="outlined-name"
                                                type="text"
                                                label="Address"
                                                name="address"
                                                fullWidth
                                                value={address}
                                                onChange={this.onInputValueChange}
                                                variant="outlined"
                                            />
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>
                                <Col md={{ size: 2, offset: 4 }}>
                                    <Button  color="primary" style={{backgroundColor:'rgba(73, 154, 236, 1)',margin:'20px'}} fullWidth variant='contained'>
                                        Upload New
                                    </Button>
                                </Col>
                            <Row>
                            </Row>
                        </div>
                    </Row>
                )}
                
            </div>
        )
    }
}
const MyInquiryWithSocket = (props) => (
    <SocketContext.Consumer>
      {socket => <MyInquiry {...props} socket={socket} />}
    </SocketContext.Consumer>
)

const mapStateToProps = (state) =>({
    auth            :state.auth,
    inquiry         :state.inquiry,
    newChatLog      :state.global.newChatLog
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({      
      getAllInquiry             :InquiryAction.getAllInquiry,
      setCurrentInquiryPage     :InquiryAction.setCurrentPage,
      setAskInvoicePanel        :InquiryAction.setAskInvoicePanel,
      addNewInvoiceMsg          :InquiryAction.addNewInvoiceMsg

      // showDlg:GlobalAction.showDialog
      //  registerUser :AuthAction.registerUser
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(MyInquiryWithSocket)

