import React, {Component} from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Link} from 'react-router-dom';

import * as AuthAction from '../../redux/actions/authAction';
import * as AdminAction from '../../redux/actions/adminAction';

import { ProSidebar, Menu, MenuItem, SubMenu,SidebarHeader, SidebarFooter, SidebarContent  } from 'react-pro-sidebar';
import SocketContext from '../../socket-context'

import {Col, Container, Row} from 'reactstrap'

import 'react-pro-sidebar/dist/css/styles.css'
import './style.css'
class MyIcon extends Component{
    render(){
        return(
            <div>1</div>
        )
    }
}
class AdminSideBar extends Component{
    componentDidMount(){
          /** */
          this.props.socket.emit("pushuser",{
            user:this.props.auth.user
        })
        /** */
        this.props.getInquiryList()
    }
    toggleSideBar = () =>{
        this.props.toggleAdminSideBar()
    }
    render(){
        const { showSideBar } = this.props.admin
        console.log(showSideBar)
        return(
            
                <ProSidebar collapsed={false} breakPoint={'md'} toggled={showSideBar} onToggle={this.toggleSideBar}>
                    <SidebarHeader>
                       <p className="logo">
                           LOGO
                        </p>
                    </SidebarHeader>
                    <SidebarContent>
                        <Menu iconShape="square">
                            <MenuItem>
                                <Link className="menu" to="/admin/">
                                    <div className="menuText">Dashboard</div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu" to="/admin/request">
                                    <div className="menuText">Request</div>
                                    <div className="badge">
                                        {Array.isArray(this.props.admin.askInquires) && (
                                            <div className="badge menuSub greenColor">
                                                {this.props.admin.askInquires.length}
                                            </div>
                                        )}
                                        {/* <div className="badge menuSub redColor">12</div> */}
                                    </div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu"  to="/admin/order">
                                    <div className="menuText">Order</div>
                                    <div className="badge">
                                        {Array.isArray(this.props.admin.orders) && (
                                            <div className="badge menuSub greenColor">
                                                {this.props.admin.orders.length}
                                            </div>
                                        )}
                                        {/* <div className="badge menuSub redColor">12</div> */}
                                    </div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                <Link className="menu"  to="/">
                                    <div className="menuText">Home</div>
                                </Link>
                            </MenuItem>
                            <MenuItem>
                                
                                    <div className="menuText" onClick={()=>{this.props.signout()}}>SignOut</div>
                                
                            </MenuItem>
                        </Menu>
                    </SidebarContent>
                    <SidebarFooter>
                        {/**
                         *  You can add a footer for the sidebar ex: copyright
                         */}
                    </SidebarFooter>
                </ProSidebar>
            
        )
    }
}
const AdminSideBarWithSocket = (props) => (
    <SocketContext.Consumer>
        {socket => <AdminSideBar {...props} socket={socket}/>}
    </SocketContext.Consumer>
)
const mapStateToProps = (state) =>({
    auth:state.auth,
    admin:state.admin,
})

function mapDispatchToProps(dispatch)
{
    return bindActionCreators({
        signout:AuthAction.logoutUser,
        toggleAdminSideBar:AdminAction.toggleAdminSideBar,
        getInquiryList:AdminAction.getInvoiceList
    }, dispatch);
}
export default connect(mapStateToProps,mapDispatchToProps)(AdminSideBarWithSocket)

